<template>
  <div id="applyRecord">
    <van-list
            v-model="loading"
            :finished="finished"
            :finished-text="'没有更多了'"
            @load="pullList">
      <div class="card" v-for="(record, k) in records" :key="k">
        <div class="box-justify p15 bgc-white">
          <div class="gray">{{ getServiceType(record.serviceType)}}</div>
          <div>申请时间：{{ record.applytime }}</div>
        </div>
        <van-cell-group>
          <van-cell title="姓名" :value="record.username"/>
          <van-cell title="电话" :value="record.phone"/>
          <van-cell title="企业" :value="record.company"/>
          <van-cell title="邮箱" :value="record.email"/>
          <van-cell title="描述" :label="record.need"/>
        </van-cell-group>
      </div>
    </van-list>
    <div v-show="records.length == 0 && finished" class="gray tc mt20">没有记录</div>
    <div v-show="records.length > 0 && finished" class="gray tc">没有更多了</div>
  </div>
</template>
<script>
  import * as utils from '@/common/utils.js'

  export default {
    name: 'applyRecord',
    data() {
      return {
        records: [],
        finished: false,
        loading: false,
        page: 1
      }
    },
    created() {
      this.tag = utils.getQueryString(window.location.href, 'tag')
    },
    methods: {
      getServiceType(type) {
        if (this.tag == 1) {
          return type === 1 ? '企业办公' : '企业送水'
        } else {
          return type === 1 ? 'IT服务' : '法律服务'
        }
      },
      pullList() {
        let url, {zone_id} = this.$route.query
        let tag = utils.getQueryString(window.location.href, 'tag')
        if (tag == 1) {
          url = this.apiConfig.companyServiceRecord
        } else {
          url = this.apiConfig.unionServiceRecord
        }
        this.axios.get(url, {
          params: {
            zone_id,
            page: this.page,
            pageSize: 5
          }
        }).then(res => {
          this.records = this.records.concat(res.d.data)
          this.page++
          this.loading = false
          if (this.records.length >= res.d.total) {
            this.finished = true
          }
        })
      }
    }
  }
</script>
<style lang="scss">
  @import '../../../assets/scss/Common/variables.scss';

  #applyRecord {
    .card {
      margin: rem(15px);
      border-radius: rem(8px);
      overflow: hidden;
    }
    .van-cell__title {
      color: $gray;
    }
    .van-cell__label {
      font-size: rem(16px);
      margin-top: rem(10px);
      line-height: 1.6;
    }
  }
</style>
